/* CHAT STYLES */

* {
    font-family: Avenir, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial, Noto Sans, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
    letter-spacing: 0.5px;
}

.ce-person-container {
    background-color: rgb(240, 240, 250);
    border-radius: 50px;
    margin-top: 2px;
}

.ce-chat-list {
    background-color: rgb(240, 240, 240) !important;
}

.ce-chats-container {
    background-color: white !important;
}

.ce-chat-card {
    background-color: rgb(240, 240, 250) !important;
    padding-left: 30px !important;
    padding-right: 30px !important;
    /* border-radius: 50px !important; */
    height: auto;
    margin-top: 5PX;
}

.ce-active-chat-card {
    background-color: cyan !important;
    /* height: ; */
    /* border: 40px green !important; */
    padding-left: 30px !important;
    padding-right: 30px !important;
}

.ce-chat-subtitle-text {
    color: #595959 !important;
}

.ce-chat-form-container {
    padding-bottom: 20px !important;
}

.ce-text-input {
    border-radius: 6px !important;
    border: 1px solid #3b2a50 !important;
}

.ce-primary-button {
    height: 42px !important;
    padding: 8px 14px !important;
    border-radius: 25px !important;
    /* background-color: #7554a0 !important; */
    position: relative;
    bottom: 1px;
}

.ce-danger-button {
    background-color: white !important;
    border-radius: 22px !important;
}

.ce-settings {
    background-color: rgb(255, 255, 255) !important;
}

.ce-autocomplete-input {
    border-radius: 6px !important;
    border: 1px solid #3b2a50 !important;
}

.ce-autocomplete-options {
    border-radius: 6px !important;
    border: 1px solid #3b2a50 !important;
    background-color: white !important;
}

.ce-chat-settings-container {
    padding-top: 12px !important;
}

.ce-chat-avatars-row {
    display: none !important;
}


/* CUSTOM FEED */

.chat-feed {
    overflow: hidden;
    height: 100%;
    width: 100%;
    overflow: auto;
    /* background-color: rgb(240, 240, 240); */
    background-image: url('https://media.istockphoto.com/vectors/learning-online-elearning-video-call-chat-with-class-distance-vector-id1218737747?k=6&m=1218737747&s=612x612&w=0&h=4B37Ea4B09hlJmf-ORWNZntd4tywqc4zGbJ0g1Wxbts=');
}

.chat-title-container {
    background-color: #333;
    width: calc(100%);
    /* padding: 15px; */
    text-align: center;
    max-height: 140px;
    margin-bottom: 10px;
}

.chat-title {
    height: 50px;
    color: aliceblue;
    font-weight: 800;
    font-size: 28px;
    padding-top: 5px;
}

.chat-subtitle {
    background-color: deepskyblue;
    color: BLACK;
    font-weight: 600;
    font-size: 15px;
    padding-top: 4px;
    height: 31px;
    width: 100%;
}

.message-row {
    float: left;
    width: 100%;
    display: flex;
    margin-left: 18px;
}

.message-block {
    width: 100%;
    display: inline-block;
}

.message-avatar {
    width: 44px;
    height: 44px;
    border-radius: 22px;
    color: white;
    text-align: center;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 48px;
}

.message {
    padding: 12px;
    font-size: 16px;
    border-radius: 6px;
    max-width: 400px;
    overflow-wrap: anywhere;
}

.message-image {
    margin-right: 18px;
    object-fit: cover;
    border-radius: 6px;
    height: 30vw;
    /* width: 30vw; */
    max-height: 200px;
    max-width: 200px;
    min-height: 100px;
    min-width: 100px;
}

.read-receipts {
    position: relative;
    bottom: 6px;
}

.read-receipt {
    width: 13px;
    height: 13px;
    border-radius: 13px;
    margin: 1.5px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 14px;
}

.message-form-container {
    position: absolute;
    bottom: 0px;
    width: calc(100%);
    padding: 18px;
    background-color: rgb(240, 240, 240);
}

.message-form {
    overflow: hidden;
    border-radius: 6px;
    border: 1px solid #3b2a50;
    background-color: white;
}

.message-input {
    height: 40px;
    width: calc(100% - 132px);
    background-color: white;
    border: 1px solid white;
    padding: 0px 18px;
    outline: none;
    font-size: 15px;
}

.image-button {
    cursor: pointer;
    padding: 0px 12px;
    height: 100%;
}

.send-button {
    height: 42px;
    background-color: white;
    border: 1px solid white;
    padding: 0px 18px;
    cursor: pointer;
}

.send-icon {
    top: 1px;
    position: relative;
    transform: rotate(-90deg);
}

.picture-icon {
    top: 1px;
    position: relative;
    font-size: 14px;
}

.chat-avatar {
    height: 45px;
    float: right;
    border-radius: 50%;
    margin-right: 20px;
    width: auto;
}


/* FORM STYLES */

*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-size: 62, 5%;
}

.wrapper {
    height: 100vh;
    width: 100%;
    background: rgb(117, 84, 160);
    background: linear-gradient(90deg, rgba(117, 84, 160, 1) 7%, rgba(117, 84, 160, 1) 17%, rgba(106, 95, 168, 1) 29%, rgba(99, 103, 174, 1) 44%, rgba(87, 116, 184, 1) 66%, rgba(70, 135, 198, 1) 83%, rgba(44, 163, 219, 1) 96%, rgba(22, 188, 237, 1) 100%, rgba(0, 212, 255, 1) 100%);
    display: flex;
    justify-content: center;
    align-items: center;
}

.input {
    color: #333;
    font-size: 1.2rem;
    margin: 0 auto;
    padding: 1.5rem 2rem;
    border-radius: 0.2rem;
    background-color: rgb(255, 255, 255);
    border: none;
    width: 90%;
    display: block;
    border-bottom: 0.3rem solid transparent;
    transition: all 0.3s;
    outline: none;
    margin-bottom: 25px;
}

.form {
    width: 400px;
}

.title {
    text-align: center;
    color: white;
    margin-bottom: 30px;
    width: 100%;
    font-size: 2.3em;
    ;
}

.button {
    border-radius: 4px;
    border: none;
    background-color: white;
    color: black;
    text-align: center;
    text-transform: uppercase;
    font-size: 22px;
    padding: 20px;
    width: 200px;
    transition: all 0.4s;
    cursor: pointer;
    margin: 5px;
    width: 90%;
}

.button span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.4s;
}

.button span:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
}

.button:hover span {
    padding-right: 25px;
}

.button:hover span:after {
    opacity: 1;
    right: 0;
}


/* login */

.bg {
    overflow: hidden;
    position: relative;
    background-image: url("https://wallpapercave.com/wp/wp5805427.gif");
    /* background-image: url("https://wallpapercave.com/wp/wp2757959.gif"); */
    display: flex;
    justify-content: center;
    /* align-items: center; */
    height: 663px;
    align-items: flex-start;
}

.box {
    max-width: 400px;
    padding: 20px;
    background-color: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(6px);
    border-radius: 25px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, .5);
    text-align: center;
    font-family: sans-serif;
    color: #218388;
    margin-top: 15%;
}

.inp {
    width: 270px;
    min-width: 190px;
    color: grey;
    height: 40px;
    padding: 5px 5px 5px 15px;
    border: 0;
    box-shadow: 0px 3px 4px rgba(0, 0, 0, .3);
    border-radius: 20px;
    margin-top: 20px
}

.inp:focus {
    outline: none;
}

.btn {
    width: 130px;
    margin-top: 20px;
    padding: 10px;
    background-color: #218388;
    color: white;
    border: none;
    border-radius: 20px;
    transition-duration: 0.1s;
    height: 40px;
}

.btn:hover {
    color: #218388;
    background-color: rgba(255, 255, 255, .1);
    border: solid 2px #218388;
    transition-duration: 0.1s;
}

.link {
    position: absolute;
    z-index: 1;
    color: aliceblue;
    text-decoration: auto;
}